@charset "UTF-8";

.uta-btn {
  border: none;
  color: #ffffff;
  display: inline-block;
  padding: 15px 50px;
  border-radius: 50px;
  -webkit-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  cursor: pointer;
  background: #065FD4
}

.uta-btn:hover {
  color: #ffffff;
}

.uta-btn i {
  margin-right: 20px;
}

.uta-btn.shadow {
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  -webkit-box-shadow: 0 7px 10px 6px rgba(0, 0, 0, 0.08);
  box-shadow: 0 7px 10px 6px rgba(0, 0, 0, 0.08);
}

.uta-play-btn>span {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  border-radius: inherit;
  border-color: inherit;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-flow: row wrap;
  flex-flow: row wrap;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.uta-play-btn-txt {
  font-weight: 700;
  color: #000;
}

.uta-play-btn-icon {
  border-radius: 50%;
  margin-top: -5%;
  margin-bottom: -5%;
  letter-spacing: 0;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  font-size: 20px;
  width: 3.5em;
  height: 3.5em;
  -webkit-box-ordinal-group: 0;
  -ms-flex-order: -1;
  order: -1;
  margin-left: 0;
  margin-right: 20px;
  border: none;
  color: #fff;
}

.uta-btn.bordered {
  background: #fff;
  border: 2px solid;
}

.uta-btn.bordered:hover {
  color: #333;
}

@-webkit-keyframes rotate-90 {
  0% {
    -webkit-transform: rotateY(0deg);
    transform: rotateY(0deg);
  }
  100% {
    -webkit-transform: rotateY(-180deg);
    transform: rotateY(-180deg);
  }
}

@keyframes rotate-90 {
  0% {
    -webkit-transform: rotateY(0deg);
    transform: rotateY(0deg);
  }
  100% {
    -webkit-transform: rotateY(-180deg);
    transform: rotateY(-180deg);
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 1;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  100% {
    opacity: 1;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 1;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  100% {
    opacity: 1;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
}

.uta-button-1 {
  display: inline-block;
  padding: 18px 42px;
  color: #fff;
  text-align: center;
  position: relative;
  overflow: hidden;
  z-index: 1;

}
.btn-bg-two {
  background-color: #ff7900;
}
.uta-button-1::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0%;
  width: 100%;
  height: 100%;
  background-color: transparent;
  -webkit-transition: all .7s;
  transition: all .7s;
  border-style: solid;
  border-width: 80px 100px;
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg);
  -webkit-transform-origin: top left;
  transform-origin: top left;
}
.btn-bg-two::before {
  border-color: #cd10e7;
}
.uta-button-1:hover::before {
  border-color: #0f0c0a;
  -webkit-transform: rotate(40deg);
  transform: rotate(40deg);
  opacity: 1;
}
.uta-button-1:hover {
  color: #fff !important;
}
/*
 * Button 2
*/
.uta-button-2 {
  padding: 20px 40px;
  display: inline-block;
  background: #640dbb;
  color: #fff;
  font-weight: 500;
  letter-spacing: 1px;
  position: relative;
  font-size: 16px;
  z-index: 1;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}
.uta-button-2::after {
  content: "";
  clear: both;
  display: block;
  overflow: hidden;
  position: absolute;
  width: 0;
  height: 100%;
  background: #02327b;
  left: 0;
  top: 0;
  z-index: -1;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}
.uta-button-2:hover{
  color: #fff;
}
.uta-button-2:hover::after{
  width: 100%;
}

